<template>
  <div id="index">
    <NavBar></NavBar>
    <br />
    <router-view></router-view>
    <div class="container col-md-6">
      <div class="jumbotron">
        <h2>Welcome to my casino!</h2>
        <p>
          You can get flag by following two ways:
          <br />1. winning the gambling
          <br />2. having more than $999,999 money
          <br />GOOD LUCK
          <br />
          <br />of course, if you transfer $999,999 to the developer, he'll give you FLAG right away:)
          <br /><br />
          <button @click="change" type="button" class="btn btn-info">Show the pay QR code</button>
          <br /><br />
          <img v-if="show"
            src="data:image/png;base64,"
          />
        </p>
      </div>
      <br />
      <h3>
        Casino status:
        <span class="badge badge-success">{{ status }}</span>
      </h3>
      <br />
      <h4>Pending user</h4>
      <table class="table">
        <thead>
          <tr>
            <th scope="col">#</th>
            <th scope="col">User</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(p, i) in pendings">
            <th scope="col">{{ i }}</th>
            <th scope="col">{{ p }}</th>
          </tr>
        </tbody>
      </table>
      <br />
      <h4>Formal player</h4>
      <table class="table">
        <thead>
          <tr>
            <th scope="col">#</th>
            <th scope="col">User</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(p, i) in players">
            <th scope="col">{{ i }}</th>
            <th scope="col">{{ p }}</th>
          </tr>
        </tbody>
      </table>
      <br />
      <h4>Winners</h4>
      <table class="table">
        <thead>
          <tr>
            <th scope="col">#</th>
            <th scope="col">User</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(w, i) in winners">
            <th scope="col">{{ i }}</th>
            <th scope="col">{{ w }}</th>
          </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>

<script>
import NavBar from "./NavBar.vue";
import { api } from "../config";

export default {
  name: "Index",

  created() {
    this.getStatus();
  },

  methods: {
    getStatus: function() {
      fetch(api + "/service/status", {
        method: "GET",
        credentials: "include"
      })
        .then(resp => resp.json())
        .then(respJSON => {
          var status = respJSON["data"]["status"];
          if (status) this.status = "running";
          else this.status = "stopped";
        });

      fetch(api + "/service/player-status", {
        method: "GET",
        credentials: "include"
      })
        .then(resp => resp.json())
        .then(respJSON => {
          this.pendings = respJSON["data"]["pendings"];
          this.players = respJSON["data"]["players"];
          this.winners = respJSON["data"]["winners"];
        });
    },

    change: function() {
      this.show = !this.show
    }
  },

  data() {
    return {
      status: "stopped",
      pendings: [],
      players: [],
      winners: [],
      show: false
    };
  }
};
</script>

<style>
</style>



